    "documentation": "An arpeggio on a chord (also known as a broken chord) is denoted by appending \\arpeggio to the chord construct:\n\n    <c e g c>1\\arpeggio\n\nDifferent types of arpeggios may be written. \\arpeggioNormal reverts to a normal arpeggio:\n\n    <c e g c>2\\arpeggio\n    \\arpeggioArrowUp\n    <c e g c>2\\arpeggio\n    \\arpeggioArrowDown\n    <c e g c>2\\arpeggio\n    \\arpeggioNormal\n    <c e g c>2\\arpeggio"
