    "detail": "Dynamics",
    "documentation": "Textual marks for dynamic changes can also replace hairpins:\n\n    \\crescTextCresc\n    c4\\< d e f\\! |\n    \\dimTextDecresc\n    g4\\> e d c\\! |\n    \\dimTextDecr\n    e4\\> d c b\\! |\n    \\dimTextDim\n    d4\\> c b a\\! |\n    \\crescHairpin\n    \\dimHairpin\n    c4\\< d\\! e\\> d\\! |\n\nTo create new absolute dynamic marks or text that should be aligned with dynamics, see New dynamic marks."
