    "documentation": "The ‘easy play’ note head includes a note name inside the head. It is used in music for beginners. To make the letters readable, it should be printed in a large font size. To print with a larger font, see Setting the staff size.\n\n    #(set-global-staff-size 26)\n    \\relative c' {\n      \\easyHeadsOn\n      c2 e4 f\n      g1\n      \\easyHeadsOff\n      c,1\n    }"
