    "detail": "Phrasing slurs",
    "documentation": "Phrasing slurs can also be made half-dashed (the first half dashed, the second half solid) or half-solid (the first half solid, the second half dashed):\n\n    c4\\( e g2\\)\n    \\phrasingSlurHalfDashed\n    g4\\( e c2\\)\n    \\phrasingSlurHalfSolid\n    c4\\( e g2\\)\n    \\phrasingSlurSolid\n    g4\\( e c2\\)"
